/* Dashboard CardPage Responsive Styles */

.card-page-container {
  border-radius: 10px;
  padding-left: 16px;
  height: 100%;
}

.card-page-activity-section {
  margin-bottom: 16px;
}

.card-page-recommendations-section {
  /* Recommendations section styles */
}

.card-page-activity-grid {
  /* Activity grid styles */
}

.card-page-recommendations-grid {
  /* Recommendations grid styles */
}

.card-page-activity-card {
  background-color: #E6F4FF;
  border-radius: 8px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
  box-shadow: 0;
  transition: box-shadow 0.2s;
}

.card-page-activity-card:hover {
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
}

.card-page-recommendation-card {
  background-color: #E6F4FF;
  border-radius: 8px;
  padding: 16px;
  cursor: pointer;
  box-shadow: 0;
  transition: box-shadow 0.2s;
}

.card-page-recommendation-card:hover {
  box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
}

.card-page-no-recommendations {
  background-color: #E6F4FF;
  border-radius: 8px;
  padding: 32px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.card-page-count {
  font-size: 18px;
  font-weight: 700;
  color: #3b3f76;
  min-width: 40px;
}

.card-page-recommendation-count {
  font-size: 16px;
  font-weight: 700;
  color: #3b3f76;
  min-width: 40px;
}

/* Media Queries */

/* Mobile devices (up to 767px) */
@media (max-width: 767px) {
  .card-page-container {
    padding-left: 8px;
  }

  .card-page-no-recommendations {
    flex-direction: column;
    gap: 16px;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .card-page-container {
    padding-left: 16px;
  }

  .card-page-no-recommendations {
    flex-direction: row;
  }
}

