/* Profile Sidebar Responsive Styles */

.profile-sidebar-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 260px;
  min-height: 600px;
  padding-top: 16px;
  padding-bottom: 16px;
}

.profile-sidebar-desktop {
  display: flex;
}

.profile-sidebar-mobile {
  display: none;
}

/* Media Queries */

/* Mobile devices (up to 767px) */
@media (max-width: 767px) {
  .profile-sidebar-desktop {
    display: none;
  }

  .profile-sidebar-mobile {
    display: block;
  }
}

/* Tablet and Desktop (768px and above) */
@media (min-width: 768px) {
  .profile-sidebar-desktop {
    display: flex;
  }

  .profile-sidebar-mobile {
    display: none;
  }
}

