/* Dashboard Responsive Styles */

.dashboard-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
  padding: 0.5rem;
}

@media (max-width: 900px) {
  .dashboard-container {
    height: auto;
    min-height: 100vh;
    overflow-y: auto;
  }
  
  .dashboard-bottom-grid {
    flex: none;
    min-height: auto;
  }
  
  .dashboard-column {
    height: auto;
    min-height: auto;
  }
}

.dashboard-top-grid {
  flex-shrink: 0;
}

.dashboard-bottom-grid {
  flex: 1;
  min-height: 0;
  overflow: hidden;
  width: 100%;
  margin: 0;
  margin-top: 1rem;
}

.dashboard-column {
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 0;
  padding: 0;
}

/* Carousel Styles */
.scholarship-carousel-card {
  display: flex;
  align-items: flex-start;
  position: relative;
  overflow: visible;
  box-shadow: none;
  color: #fff;
  margin-top: 1rem;
  padding: 1rem;
  min-height: 140px;
}

.scholarship-carousel-content {
  flex: 1;
  padding-right: 0.5rem;
}

.scholarship-carousel-title {
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #fff;
  text-align: left;
  font-size: 0.95rem;
}

.scholarship-carousel-subtitle {
  font-weight: 300;
  margin-bottom: 0.5rem;
  color: #fff;
  text-align: left;
  font-size: 0.85rem;
}

.scholarship-carousel-description {
  margin-bottom: 1rem;
  color: #fff;
  text-align: left;
  font-size: 0.7rem;
}

.scholarship-carousel-button {
  background: #fff;
  color: #222;
  border-radius: 24px;
  font-weight: 700;
  padding: 0.5rem 1rem;
  text-transform: none;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  min-width: 100px;
  font-size: 0.75rem;
  border: none;
  cursor: pointer;
}

.scholarship-carousel-button:hover {
  background: #f5f5f5;
  color: #222;
}

.scholarship-carousel-image {
  position: absolute;
  right: 1rem;
  bottom: 0;
  width: 100%;
  max-width: 120px;
  height: auto;
  border-radius: 0;
}

.scholarship-carousel-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
}

.scholarship-carousel-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 0 0.25rem;
}

.scholarship-carousel-dot.active {
  background: #A993FF;
}

.scholarship-carousel-dot.inactive {
  background: #F2EFFF;
}

/* Pending Task Container */
.pending-task-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  min-height: 0;
  height: 100%;
  overflow: hidden;
}

.pending-task-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
  overflow: hidden;
}

/* Media Queries */

/* Extra Extra Small devices (phones portrait, up to 360px) */
@media (max-width: 360px) {
  .dashboard-container {
    padding: 0.25rem;
  }
  
  .dashboard-bottom-grid {
    margin-top: 0.5rem;
  }
  
  .dashboard-column {
    margin-bottom: 0.75rem;
  }

  .scholarship-carousel-card {
    margin-top: 0.5rem;
    padding: 0.5rem;
    border-radius: 16px;
    min-height: 130px;
  }

  .scholarship-carousel-content {
    padding-right: 0.25rem;
  }

  .scholarship-carousel-title {
    font-size: 0.85rem;
    margin-bottom: 0.25rem;
  }

  .scholarship-carousel-subtitle {
    font-size: 0.75rem;
    margin-bottom: 0.25rem;
  }

  .scholarship-carousel-description {
    font-size: 0.65rem;
    margin-bottom: 0.75rem;
  }

  .scholarship-carousel-button {
    padding: 0.25rem 0.5rem;
    min-width: 90px;
    font-size: 0.7rem;
  }

  .scholarship-carousel-image {
    display: none;
  }

  .scholarship-carousel-dots {
    margin-top: 0.25rem;
    margin-bottom: 0.75rem;
  }
}

